body {
    background-color: black;
    margin: 0;
    padding: 0;
}

a {
    color: #aeae9b;
}

a:hover {
    color: #fff;
}

.postbody a {
    word-break: break-all;
}

.jt_red {
    color: #f00;
}

.jt_green {
    color: #8dc63f;
}

.jt_pink {
    color: #f49ac1;
}

.jt_olive {
    color: olive;
}

.jt_fuchsia {
    color: #c0ffc0;
}

.jt_yellow {
    color: #ffde00;
}

.jt_blue {
    color: #44aedf;
}

.jt_lime {
    color: #c0ffc0;
}

.jt_orange {
    color: #f7941c;
}

.jt_italic {
    font-style: italic;
}

.jt_bold {
    font-weight: bold;
}

.jt_underline {
    text-decoration: underline;
}

.jt_strike {
    text-decoration: line-through;
}

.jt_sample {
    font-size: 80%;
    line-height: 10px;
}

.jt_quote {
    font-family: serif;
    font-size: 110%;
}

.jt_spoiler {
    cursor: help;
    color: #383838;
    background-color: #383838;
}

.jt_spoiler:focus {
    color: #f00;
}

.jt_codesmall {
    font-family: monospace;
}

.jt_code {
    display: block;
    font-family: monospace;
    border-left: 1px solid #666;
    padding: 3px 0 3px 10px;
    margin: 5px 0 5px 10px;
}

ul.comments {
    background: url(data:image/gif;base64,R0lGODlhAQACAIAAACgoKAAAACH5BAAAAAAALAAAAAABAAIAAAICBAoAOw==) repeat-y 2px bottom;
    margin: 0 0 0 5px;
    list-style-type: none;
    padding: 0;
    position: static;
}

ul.comments li {
    background: url(data:image/gif;base64,R0lGODlhDAAyAIAAACgoKAAAACH5BAAAAAAALAAAAAAMADIAAAI3RI4ZpsqNHlwz1iNhbqD7713UtTlimaAWebbsW6mYG9OTPNbwbWs99zPtfENgUahL8o6pYPNSAAA7) no-repeat 2px top;
    font-size: 13px;
    line-height: 16px;
    padding-left: 13px;
}

ul.comments li:last-child {
    background: #000 url(data:image/gif;base64,R0lGODlhDAAJAIAAACgoKAAAACH5BAAAAAAALAAAAAAMAAkAAAIRRI4ZpsqNHlwz1iNhbqD73xUAOw==) no-repeat 2px top;
}

